export default {
	data() {
		return {
			id:"",
			num:"",
			userData:"",
			availNum:""
		}
	},
	onLoad(options) {
		this.id = options.id;
		this.getUserInfo();
		this.availableqtybyempid();
	},
	methods: {
		getUserInfo() {
			const that = this;
			let param={
				id:that.id
			}
			that.$api.userinfoByid(param).then(res => {
				if (res.success) {
					let result = res.datas;
					result.headImgUrl = result.headImgUrl ? result.headImgUrl : '../../../../static/images/defaultUserPic.png';
					result.nickName = result.nickName ? result.nickName : result.mobile;
					that.userData = result;
				} else {
					uni.showToast({
						title: res.message,
						icon: "none"
					})
				}
			})
		},
		availableqtybyempid(){
			const that = this;
			that.$api.availableqtybyempid({
				id:that.id
			}).then(res => {
				if (res.success) {
					that.availNum=res.datas;
				} else {
					uni.showToast({
						title: res.message,
						icon: "none"
					})
				}
			})
		},
		sub(){
			const that = this;
			let numText=/^[0-9]*[1-9][0-9]*$/;
			console.log(that.num);
			if(!numText.test(that.num)){
				uni.showToast({
					title:"请输入整数",
					icon:"none"
				})
			}
			else{
				that.$api.givediamond({
					employeeId:that.id,
					"qty":that.num
				}).then(res => {
					if (res.success) {
						uni.showToast({
							title:"分配成功",
						})
						setTimeout(()=>{
							that.num="";
							that.availableqtybyempid();	
						},1000)
					} else {
						uni.showToast({
							title: res.message,
							icon: "none"
						})
					}
				})
			}
		},
		gotoDetailed(){
			const that = this;
			that.$Router.push({
				path:"/pages/my/manageNavs/staff/detailed",
				query:{
					id:that.id
				}
			})
		}
	}
}
